.form {
  display: flex;
  flex-direction: column;
  padding: 10px;
  width: 500px;
  border: solid 2px black;
  margin-left: 30px;
}

.label {
  font-weight: 600;
  font-size: 20px;
}

.input {
  display: block;
  margin-top: 10px;
  margin-bottom: 20px;
}

.input:focus {
  border: solid 2px rgb(6, 100, 240);
  border-radius: 3px;
  outline: none;
}

.button {
  width: 100px;
  border-radius: 3px;
  background-color: rgb(253, 252, 252);
  outline: none;
}

.button:hover,
.button:focus {
  background-color: blue;
  color: white;
}
